import React, { useState, useEffect } from 'react';
import { Chart as ChartJS, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend, Filler } from 'chart.js';
import { Line } from 'react-chartjs-2';

function BitcoinChart() {
  const [marketPrice, setMarketPrice] = useState([]);

  useEffect(() => {
    async function fetchData() {
      const response = await fetch(
        'https://api.blockchain.info/charts/avg-block-size?timespan=5months&format=json&cors=true'
      );
      const data = await response.json();
      const marketPriceData = data.values.map((value) => value.y);
      setMarketPrice(marketPriceData);
    }
    fetchData();
  }, []);

  const options = {
    responsive: true,
    maintainAspectRatio: true,
    interaction: {
      mode: 'index',
      intersect: false,
    },
    plugins: {
      title: {
        display: true,
        text: 'Market Price(USD)',
      },
      scales: {
        x: {
          type: 'linear',
          position: 'bottom',
        },
        y: {
          type: 'linear',
          position: 'left',
          display: true,
        },
      },
      filler: {
        propagate: true,
      },
    },
    animation: false,
  };

  ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
    Filler
  );

  ChartJS.register(Filler); // Register the filler plugin separately

  const data = {
    labels: Array.from({ length: marketPrice.length }, (_, i) => i + 1),
    datasets: [
      {
        label: 'Market Price',
        data: marketPrice,
        borderColor: '#601640',
        backgroundColor: '#9d1b65',
        fill: true,
        yAxisID: 'y',
      },
    ],
  };

  return (
    <div>
      <Line className='chart' data={data} options={options} />
    </div>
  );
}

export default BitcoinChart;
